import apiName from './../../static/commonjs/request/apiName.js';
import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			message:''
		}
	},
	computed: {
		num(){
			return this.message.length
		},
		...mapState(['token'])
	},
	methods: {
		goBack(){
			uni.navigateBack()
		},
		submit(){
			var _this = this;
			_this.$http.post(apiName.CALLBACK_ADVISE,{
				params:{
					token:_this.token,
					content:_this.message
				}
			}).then(res => {
				if(_this.message == ''){
					return uni.showToast({
					    icon:'none',
						title:'请输入内容'
					})
					
				}
				if(res.data.success){
					uni.showToast({
						title:res.data.msg,
						mask:true,
						success: function () {
						      setTimeout(function() {
						        uni.navigateBack();
						      }, 2000);
						}
					});
				}
			}).catch(err => {
				console.log(err);
			});
			
		}
	}
}
